import testStories from './test-stories.json';
import realStories from './stories.json';

let stories = realStories;

if (process.env.NODE_ENV === 'test') {
  stories = testStories;
}

stories.forEach(story => {
  story.url = 'https://littlemovingthings.imgix.net/' + story.time + '.JPG';
});

stories.reverse();

export { stories };
