import React, {Component} from 'react';
import {Route, Switch} from 'react-router';
import './styles/styles.css';
import {ConnectedStoryListView} from './StoryListView';
import {StoryDetailsView} from './StoryDetailsView';
import {AboutView} from "./AboutView";
import Redirector from "./Redirector";
import {Navigation} from "./components/Navigation";
import {Footer} from './components/Footer.js';

const RenderHomeOrRedirect = ({location}) => <Redirector location={location}><ConnectedStoryListView/></Redirector>;

export class App extends Component {
    render() {
        return (
            <div>
                <Navigation/>
                <Switch>
                    <Route path="/stories/:id" component={StoryDetailsView}/>
                    <Route path="/about" component={AboutView}/>
                    <Route path="/" component={RenderHomeOrRedirect}/>
                </Switch>
                <Footer/>
            </div>

        );

    }
}
