import React from 'react';
import './styles/styles.css';
import {App} from './App';
import {BrowserRouter as Router} from "react-router-dom";
import {render} from 'react-snapshot';
import {Helmet} from "react-helmet";

const OGdescription = "A collection of photos & stories of small cars, little trucks or little things that may or may not move. It is an art project by Jesper Bröring with help from other contributors. The stories are real or funny, you can be the judge of that.";

const isTestEnv = process.env.NODE_ENV === 'test';
const isLocalhost = window.location.hostname === 'localhost';
let analyticsCabin = "";

if (!isTestEnv && !isLocalhost) {
    analyticsCabin = <script async defer src="https://scripts.withcabin.com/hello.js"/>;
}

render(
    <Router>
        <div>
            <Helmet
                title="little moving things"
                meta={[
                    {"name": "twitter:card", content: "summary_large_image"},
                    {"name": "twitter:creator", content: "@jbroring"},
                    {"name": "twitter:title", content: "little moving things"},
                    {"name": "twitter:description", content: OGdescription},
                    {
                        "name": "twitter:image",
                        content: "http://littlemovingthings.io/static/img/littlemovingthings_3x3_1200.jpg"
                    },
                    {"name": "description", property: "og:description", content: OGdescription},
                    {property: "og:type", content: "website"},
                    {property: "og:title", content: "little moving things"},
                    {property: "og:url", content: "http://littlemovingthings.io"},
                    {
                        property: "og:image",
                        content: "http://littlemovingthings.io/static/img/littlemovingthings_3x3_1200.jpg"
                    }
                ]}
            />
            <App/>
            {analyticsCabin}
        </div>
    </Router>,
    document.getElementById('root'));

