import React, {Component} from 'react';

export class Image extends Component {

  constructor(props) {
    super(props);
    this.state = {
      imageHasRendered: false,
    };
  }

  render() {
    let {onClick} = this.props;
    const src = this.getSrc();
    const backgroundImage = src ? {backgroundImage: `url(${src})`} : {};
    return (
        <div className="image-wrapper" style={backgroundImage} data-qa={src} ref={element => this.element = element}
             onClick={onClick}/>
    );
  }

  getSrc() {

    if(!this.state.imageHasRendered) {
      return null;
    }

    let {src} = this.props;

    const size = this.element.getBoundingClientRect().width;
    src += '?fit=max&w=' + size + '&h=' + size + '&fm=jpg&auto=format';

    if (window.devicePixelRatio) {
      src += '&dpr=' + window.devicePixelRatio;
    }

    return src;
  }

  componentDidMount() {
    this.setState({imageHasRendered: true});
  }
}
