export const shortenStoryText = (text) => {
  const fullstop = text.indexOf(".");
  const firstExclamationMark = text.indexOf("!");
  const secondExclamationMark = text.indexOf("!", (firstExclamationMark + 1));

  if (fullstop > 0 || secondExclamationMark > 0) {
    if (fullstop === -1) {
      return text.substring(0, secondExclamationMark + 1);
    }

    if (secondExclamationMark === -1) {
      return text.substring(0, fullstop + 1);
    }

    return text.substring(0, Math.min(fullstop, secondExclamationMark) + 1);
  }

  return text;
};