import React, {Component} from 'react';
import {stories} from './data/stories';
import {Image} from './components/Image.js';
import {timestampToDate} from './utils/timestampToDate';
import {Link} from 'react-router-dom';
import FontAwesome from 'react-fontawesome';
import {Helmet} from "react-helmet";

export class StoryDetailsView extends Component {

    createNavigator({nextStoryId, previousStoryId}) {
        return event => {
            if (event.key === 'ArrowRight' && nextStoryId) {
                this.props.history.push(`/stories/${nextStoryId}`);
            }
            if (event.key === 'ArrowLeft' && previousStoryId) {
                this.props.history.push(`/stories/${previousStoryId}`);
            }
        };
    }

    componentWillUnmount() {
        if (this.navigateNextOrPrevious) {
            document.removeEventListener("keydown", this.navigateNextOrPrevious, false);
        }
    }

    getStory(storyIndex) {
        return stories[storyIndex];
    }

    getStoryIndex() {
        const {match: {params: {id}}} = this.props;
        return stories.findIndex(story => story.time === id);
    }

    getNextStoryId(storyIndex) {
        const nextStory = stories[storyIndex - 1];
        return nextStory ? nextStory.time : null;
    }

    getPreviousStoryId(storyIndex) {
        const previousStory = stories[storyIndex + 1];
        return previousStory ? previousStory.time : null;
    }

    render() {
        const storyIndex = this.getStoryIndex();

        if (this.navigateNextOrPrevious) {
            document.removeEventListener("keydown", this.navigateNextOrPrevious, false);
        }

        const nextStoryId = this.getNextStoryId(storyIndex);

        const previousStoryId = this.getPreviousStoryId(storyIndex);

        this.navigateNextOrPrevious = this.createNavigator({nextStoryId, previousStoryId});
        document.addEventListener("keydown", this.navigateNextOrPrevious, false);

        const story = this.getStory(storyIndex);

        return (
            <div>
                <Helmet
                    title="little moving things"
                    meta={[
                        {"name": "twitter:description", content: `${story.text}`},
                        {"name": "twitter:image", content: `${story.url}`},
                        {"name": "description", property: "og:description", content: `${story.text}`},
                        {property: "og:image", content: `${story.url}`}
                    ]}
                />

                <main>
                    <div className="story-detail" aria-label={story.text}>
                        <div className="story-detail-left">
                            <div className="story-detail-image">
                                <Image src={story.url}/>
                            </div>
                        </div>
                        <div className="story-detail-right">
                            <div className="story-detail-meta">
                                <div className="story-detail-date">
                                    <p data-qa="story-date">{timestampToDate(story.time)}</p>
                                </div>
                                <div className="story-detail-location">
                                    <p data-qa="story-location">{story.location}</p>
                                </div>
                            </div>
                            <div className="story-detail-text">
                                <p data-qa="story-text">{story.text}</p>
                            </div>
                        </div>
                    </div>
                    <div className="story-detail-PreNext">
                        {previousStoryId && <Link className="btn-previous" data-qa="btn-previous"
                                                  to={`/stories/${previousStoryId}`}><FontAwesome
                            name="chevron-left"/> Previous story</Link>}
                        {nextStoryId && <Link className="btn-next" data-qa="btn-next" to={`/stories/${nextStoryId}`}>Next
                            story <FontAwesome name="chevron-right"/></Link>}
                    </div>
                </main>
            </div>
        );
    }
}
