import React, {Component} from 'react';
import {stories} from './data/stories.js';
import {Image} from './components/Image.js';
import {Link} from "react-router-dom";
import LazyLoad from 'react-lazyload';
import {shortenStoryText} from "./utils/shortenStoryText";
import {timestampToDate} from "./utils/timestampToDate";

export class StoryListView extends Component {

    renderStory(story) {
        return (
            <Link type="button" key={story.time} to={`/stories/${story.time}`}
                  aria-label={shortenStoryText(story.text)} className="story-thumbnail"
                  name={timestampToDate(story.time)}
                  data-qa={story.time}>
                <LazyLoad height={200} offset={300}>
                    <Image src={story.url}/>
                </LazyLoad>
            </Link>
        );
    }

    renderStories() {
        return (
            <div className="stories-wrapper">
                {stories.map(story => this.renderStory(story))}
            </div>
        );
    }

    render() {
        return (
            <main>
                {this.renderStories()}
            </main>
        );
    }
}

export const ConnectedStoryListView = StoryListView;
